import { createWebHistory, createRouter } from "vue-router";
import listCompVue from "@/components/list-comp.vue";
import homeCompVue from '@/components/home-comp.vue';
import detailCompVue from '@/components/detail-comp.vue';
import page404CompVue from '@/components/page404-comp.vue';
import authorCompVue from '@/components/author-comp.vue';
import commentCompVue from '@/components/comment-comp.vue';

const routes = [
  {
    path: "/list",
    component: listCompVue,
    // {
    //   List : listCompVue,
    //   Comment : commentCompVue,
    // },
  },
  {
    path: "/",
    component: homeCompVue,
  },
  {
    path: "/detail/:id",
    component: detailCompVue,
    children:[
      {
        path: "author",
        component: authorCompVue,
      },
      {
        path: "comment",
        component: commentCompVue,
      },
    ]
  },
  {
    path: "/:anything(.*)",
    component: page404CompVue,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;